.catalog-page__container {
	.catalog-page__header {
		display: flex;
		justify-content: space-between;

		.search-form {
			display: flex;
			align-items: center;
			border: 1px solid #d8d8d8;

			input {
				padding: 15px 0px 15px 15px;
				font-weight: 400;
				font-size: 14px;
				line-height: 157%;
				color: $black-primary;

				&::placeholder {
					color: $dark-grey-secondary;
				}
			}

			button {
				display: flex;
				align-items: center;
				justify-content: center;
				height: 100%;
				padding: 15px;
			}
		}
	}

	.catalog-page__content {
		display: flex;
		align-items: flex-start;
		gap: 60px;
		margin-top: 30px;

		.products-list {
			display: grid;
			grid-template-columns: repeat(3, 1fr);
			column-gap: 30px;
			row-gap: 40px;

			.product-list__item {
				display: flex;
				flex-direction: column;

				.image__block {
					width: 100%;
					max-width: 100%;
					aspect-ratio: 354 / 441;
					display: flex;
					align-items: center;
					justify-content: center;
					position: relative;
					overflow: hidden;

					.product__label {
						position: absolute;
						left: 20px;
						top: 20px;
						font-weight: 400;
						font-size: 12px;
						line-height: 167%;
						padding: 2px 10px;
						background-color: $white;
						color: $black-primary;

						@media (max-width: 980px) {
							top: 10px;
							left: 16px;
						}

						@media (max-width: 768px) {
							font-size: 10px;
							padding: 0px 6px;
						}
					}

					.view-more {
						position: absolute;
						bottom: 0;
						left: 0;
						width: 100%;
						font-weight: 400;
						font-size: 16px;
						line-height: 150%;
						letter-spacing: 0.02em;
						color: $dark-grey-secondary;
						background-color: rgba(255, 255, 255, 0.7);
						padding: 20px 0px;
						text-align: center;
						transform: translateY(100%);
						transition: transform 0.5s ease;
					}

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}

					&:hover {
						.view-more {
							transform: translateY(0);
						}
					}
				}

				.product__name {
					padding: 10px 0px;
					font-family: $font-family;
					font-weight: 300;
					font-size: 16px;
					line-height: 150%;
					letter-spacing: 0.02em;
					text-align: center;
					color: #3a3836;

					@media (max-width: 980px) {
						font-size: 14px;
						padding: 6px 0px;
					}

					@media (max-width: 467px) {
						font-size: 12px;
						padding: 2px 0px;
					}
				}

				.product__price {
					font-family: $font-family;
					font-weight: 600;
					font-size: 16px;
					line-height: 150%;
					letter-spacing: 0.02em;
					text-align: center;
					color: #3a3836;
					display: flex;
					align-items: center;
					justify-content: center;
					gap: 12px;

					@media (max-width: 980px) {
						font-size: 14px;
					}

					@media (max-width: 467px) {
						font-size: 12px;
					}

					.old-price {
						font-family: $font-family;
						font-weight: 400;
						font-size: 12px;
						line-height: 183%;
						text-decoration: line-through;
						text-align: center;
						color: $red-primary;

						@media (max-width: 980px) {
							font-size: 10px;
						}

						@media (max-width: 467px) {
							font-size: 9px;
						}
					}
				}
			}
		}
	}
}
